import React from 'react'

import './view.scss'

export const View = () => {
  return (
    <div className="view">
      <form>
        <select className="viewBox" >
          <option value="large">1 block</option>
          <option value="medium">2 blocks</option>
        </select>
      </form>
    </div>
  )
}



